@value makerWhite, makerGrey from "../../common/colors.css";

.container {
  display: flex;
  justify-content: center;
  cursor: pointer;
  width: auto;
  max-width: 1000px;
  min-width: 500px;
  margin-bottom: 10px;
}

.link {
  display: flex;
  text-decoration: none;
  width: auto;
  max-width: 1000px;
  min-width: 500px;
}

.metadata {
  display: flex;
  flex-direction: row;
  width: auto;
  max-width: 1000px;
  min-width: 500px;
  height: 200px;
  padding: 10px;
}

.text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 60%;
  padding-left: 10px;
  color: makerGrey;
}

.title {
  display: flex;
  font-size: 18px;
  margin: 0;
  padding-bottom: 10px;
  color: makerWhite;
}

.channel {
  display: flex;
  font-size: 14px;
  color: makerGrey;
  margin: 0;
}

.descriotipn {
  display: flex;
  font-size: 12px;
  color: makerGrey;
  margin: 0;
}

pre {
  white-space: pre-wrap; /* css-3 */
  white-space: -moz-pre-wrap; /* Mozilla, since 1999 */
  white-space: -pre-wrap; /* Opera 4-6 */
  white-space: -o-pre-wrap; /* Opera 7 */
  word-wrap: break-word; /* Internet Explorer 5.5+ */
}
